
.main {
  height: 100%;
}
.info {
  background-color: rgba(255,255,255,0.9);
  height: 100%;
  width: 300px;
  position: fixed;
  right: 0;
  padding: 20px;
  display: none;
}
label {
  width: 70px;
}
.tool {
  position: absolute;
  padding-bottom: 50px;
  bottom: 0;
  width: 260px;
}
.buynum {
  width: 40px;
  text-align: center;
  border: 1px solid #ccc;
  height: 29px;
}
.addcart {
  width: 100%;
  margin-top: 20px;
}
.cart {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: rgba(255,255,255,0.9);
  padding-left: 20px;
}
.cartinner {
  height: 100px;
  padding: 20px;
  display: none;
}
li {
  list-style: none;
}
ul, li {
  margin: 0;
  padding: 0;
}
ul {
  margin-left: 10px;
}
li {
  float: left;
}
.carticon {
  position: absolute;
  top: -70px;
  left: 30px;
}
.checkout {
  position: absolute;
  right: 20px;
  top: 30px;
}
.rqcode {
  position: fixed;
  display: none;
  width: 500px;
  height: 450px;
  margin: 5% auto; /* Will not center vertically and won't work in IE6/7. */
  left: 0;
  right: 0;
  background-color: rgba(255,255,255,0.9);
  text-align: center;
  border-radius: 15px;
}



